.thanks-content {
    min-height: 100vh;
    width: 100%;
    .message {
        background-color: #dff0d8;
        border: 1px solid transparent;
        border-color: #d6e9c6;
        border-radius: 5px;
        box-sizing: border-box;
        color: #3c763d;
        padding: 15px;
        text-align: center; }
    h2 {
        font-weight: bold;
        font-size: 1.5em;
        margin-bottom: 15px; }
    p {
        line-height: 20px;
        padding: 0 20px; } }

@media (min-width: 2600px) {
    .thanks-content {
        .message {
            margin-top: 100px;
            border-radius: 0; }
        h2 {
            font-size: 3em;
            padding: 10px 15px; }
        p {
            font-size: 2em;
            text-align: center;
            margin-bottom: 20px;
            line-height: 35px; } } }

@media (min-width: 1920px) and (max-width: 2599px) {
    .thanks-content {
        .message {
            margin-top: 50px;
            border-radius: 0; }
        h2 {
            font-size: 2.5em;
            padding: 10px 15px; }
        p {
            font-size: 1.5em;
            text-align: center;
            margin-bottom: 20px;
            line-height: 35px; } } }

@media (min-width: 1280px) and (max-width: 1919px) {
    .thanks-content {
        .message {
            margin: 30px auto;
            width: 90%; } } }

@media (min-width: 960px) and (max-width: 1279px) {
    .thanks-content {
        min-height: 100%;
        width: 100%;
        .wrapper {
            display: block; }
        .message {
            width: 80%;
            display: block;
            margin: 30px auto 0; } } }


@media (max-width: 959px) {
    .thanks-content {
        min-height: 100vh;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        .wrapper {
            display: block;
            vertical-align: inherit;
            margin-top: 5%;
            width: 90%; }
        .message {
            height: 200px;
            margin: inherit;
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center; }
        h2 {
            font-size: 1.2em; } } }
