@media (min-width: 2600px) {
  .flex-caption {
    font-size: 4em;
    bottom: 40%; } }

@media (min-width: 1920px) and (max-width: 2599px) {
  .flex-caption {
    font-size: 3em;
    bottom: 40%; } }

@media (min-width: 1680px) and (max-width: 1919px) {
  .flex-caption {
    font-size: 2.8em;
    bottom: 40%; } }

@media (min-width: 1440px) and (max-width: 1679px) {
  .flex-caption {
    font-size: 2.4em;
    bottom: 38%; } }

@media (min-width: 1360px) and (max-width: 1439px) {
  .flex-caption {
    font-size: 2.2em;
    bottom: 36%; } }

@media (min-width: 1280px) and (max-width: 1359px) {
  .flex-caption {
    font-size: 2.2em;
    bottom: 35%; } }

@media (min-width: 960px) and (max-width: 1279px) {
  .header {
    .left-background {
      width: 100%; }
    .middle-background {
      background: none; }
    .logo {
      margin-left: 0;
      left: 20px;
      top: 12px;
      width: 290px; }
    .address {
      color: #3A3A3A;
      left: 35%;
      position: absolute;
      top: 7px;
      z-index: 10;
      p {
        font-size: .9em; } }
    .main-menu {
      ul {
        width: $tabletWidth; } } }
  .flex-caption {
    font-size: 1.6em;
    bottom: 40%; }
  .carousel {
    top: 0;
    margin-bottom: 0;
    width: 100%; } }

@media (min-width: 600px) and (max-width: 959px) {
  .header {
    height: 390px;
    border-bottom: 8px solid $orange;
    .left-background {
      width: 100%; }
    .middle-background {
      background: none; }
    .right-background {
      display: none; }
    .logo {
      margin-left: 0;
      margin: 40px auto 0;
      width: 100%;
      max-width: 350px;
      position: relative;
      left: 0;
      top: 10px; }
    .address {
      color: #3A3A3A;
      position: relative;
      margin: 1em .5em;
      left: 0;
      display: block;
      p {
        font-size: 16px;
        text-align: center; }
      .social-icons {
        display: block;
        margin: 15px auto 0;
        width: 150px; } }
    .main-menu {
      top: -40px;
      bottom: initial;
      #menu-button {
        background-color: #222;
        color: $white;
        cursor: pointer;
        display: block;
        font-weight: bold;
        margin-bottom: 2px;
        padding: .75em 1em;
        text-align: center; }
      ul {
        display: none;
        width: $tabletWidth;
        li:first-child {
          margin-top: 0; }
        li.active a:hover {
          background-color: #000; }
        li {
          margin-top: -1px;
          a {
            padding-left: 1em;
            background-color: #222;
            &:hover {
              background-color: $orange; } }
          &:before,
          &:after {
            display: none; } } } } }

  .flex-caption {
    font-size: 1.4em;
    bottom: 38%; }

  .carousel {
    top: 0;
    margin-bottom: 0; }

  .home-content {
    .security-alarms-group {
      padding-top: 1.5em;
      .security-comfort,
      .alarm-controls {
        margin: 0 auto;
        width: 90%; } } }

  .viper-clifford-features {
    display: block;
    width: 100%;
    .clifford-features {}
    .description, {
      width: 100%; }
    .clifford-features {
      text-align: center;
      margin-bottom: 1em; }
    .description p {
      font-size: 18px; } }

  .alarms-content {
    width: 95%;
    margin: 0 auto;

    .alarms {
      margin-top: 1em;
      width: 100%;
      .row {
        flex-wrap: wrap;
        .column {
          flex: 0 1 48%;
          margin-bottom: 1em;
          .box {
            padding-bottom: 40px;
            box-shadow: 0 5px 5px rgba(0,0,0,.3);
            &:after {
              display: none; } }
          .title-botton {
            padding: 10px;
            height: auto;
            h3 {
              font-size: 20px; }
            a.button {
              padding: .5em 1em;
              font-size: 18px;
              &:before {
                display: none; } } } } } }
    .protect {
      padding: 0;
      margin-bottom: 1em;
      width: 100%;
      .protect-logo {
        width: 100%; }
      .banners-wrapper {
        padding: 0;
        display: block;
        width: 100%;
        .banner-v2, .banner-v4 {
          margin-bottom: 2em;
          text-align: center;
          width: 100%;
          img {
            max-width: 472px; } } } }
    .logos-wrapper {
      display: none; }

    .banners-wrapper {
      display: block;
      padding: 0;
      text-align: center;
      width: 100%;
      .ontas-logo-mobile,
      .directed-logo-mobile {
        display: block;
        margin: 1em auto;
        box-shadow: none; }
      .ontas-banner,
      .viper-banner {
        width: 100%;
        margin-bottom: 1em; }
      .ontas-banner .ontas-logo-mobile {
        width: 167px; }
      .viper-banner .directed-logo-mobile {
        width: 209px; } }

    .antiassault-film {
      width: 100%;
      .information {
        width: 55%;
        h2 {
          margin-bottom: 1em; } }
      .side {
        width: 40%;
        img {
          margin: 1em auto; } } }

    .max-limit {
      width: 100%;
      .information {
        width: 50%; } } }

  .audio-content {
    margin: 1em auto;
    width: 95%;
    .row,
    .directed-logo,
    .information {
      width: 100%; } }

  .audio-content {
    p {
      margin-top: 1em; }
    .row .column {
      width: 49%; } }

  .confort-content {
    margin: 1em auto;
    width: 95%;
    .locker-sensor {
      display: block;
      & > div {
        padding-bottom: 1em;
        height: auto;
        width: 100%; } }
    .photocel-sensor {
      display: block;
      .photocel,
      .retro-sensor {
        width: 100%; } }
    .pka .product-image {
      flex: 0 0 150px;
      margin-right: 1em; }
    .simple .product-image {
      margin-right: 2em; }
    .easy-go {
      .product-image {
        flex: 0 0 45%;
        margin-right: 20px; }
      .description {
        text-align: justify; } } }

  .contact-content {
    width: 90%;
    h2 {
      margin-top: .5em; }
    .message {
      font-size: 18px;
      span {
        display: block; } }
    .form {
      .form-inputs {
        display: block;
        div {
          display: block;
          margin-bottom: .5em;
          width: 100%; } }
      .checkbox {
        span {
          display: block;
          margin-bottom: .5em; } }
      .buttons button {
          width: 48%;
          margin-bottom: 1em;
          &:first-child {
            margin-right: 2%; } } }
    .address {
      display: block;
      .address-1, .address-2 {
        margin: 0 auto 1.5em;
        width: 100%;
        .information {
          margin-top: -5px; } } } } }

